@import "~@/assets/styles/sass/style.sass"






















.about
	display: flex
	align-items: flex-end
	justify-content: center
	flex-direction: column
	width: 100%
	height: 100%
	&__title
		width: 100%
		display: flex
		justify-content: space-between
		.back-arrow
			font-size: 4rem
			color: var(--akcentLight)
	&__content
		font-weight: 800
		display: flex
		align-items: flex-end
		justify-content: center
		p
			font-size: 2rem
			line-height: 4rem
		a
			color: var(--akcentLight)
			margin-left: .5rem
	#github
		margin-top: 4rem
		font-size: 1.5rem
