@import "~@/assets/styles/sass/style.sass"































































.beats-menu
	width: 100%
	height: 100%
	background-color: var(--mainBg)
	z-index: 4
	display: flex
	align-items: center
	justify-content: center
	flex-direction: column
	align-items: center
	max-width: 500px
	max-height: 750px
	margin: 0 auto
	padding: 4rem
	&__output
		font-size: 4rem
		color: var(--akcentLight)
	&__beats, .beats-menu__size, .beats-menu__stress
		width: 100%
	&__beats-title, .beats-menu__size-title, .beats-menu__stress-title
		font-size: 2rem
		text-transform: uppercase
		font-weight: 800
		margin: 2rem 0rem
		width: 100%
	&__btn
		width: 100%
		margin-top: 4rem
		button
			width: 100%
			color: var(--akcentLight)
			text-transform: uppercase
			font-size: 1.4rem
