@import "~@/assets/styles/sass/style.sass"



























































































.add-song
	&__content
		display: flex
		justify-content: space-between
	&__author, .add-song__title, .add-song__bpm, .add-song__beats, .add-song__size, .add-song__stress
		margin: 1rem 0rem
		h3
			font-size: 1rem
			letter-spacing: .4rem
			font-weight: 800
			text-transform: uppercase
			margin-bottom: 1rem
		input
			border: none
			outline: none
			width: 100%
			font-size: 1.2rem
			padding: 1.5rem
			color: var(--mainGrey)
			border-radius: 10px
			font-weight: 800
	&__nums 
		display: flex 
		#beatsValue
			color: var(--akcentLight)
			margin-left: 1rem
	&__bpm
		position: relative
		input::-webkit-outer-spin-button,
		input::-webkit-inner-spin-button
			-webkit-appearance: none
			margin: 0
		input[type=number]
			-moz-appearance: textfield
	.add-song__nums
		margin-bottom: .5rem  
	&__bpm-info
		width: 100%
		display: flex
		margin-top: 1rem
		position: absolute
		bottom: -20px
		right: 0%
		span
			width: 100%
			text-align: right
			font-size: 1.5rem
			color: red

	&__size-items
		margin-top: 1.5rem
	&__total
		font-size: 1.5rem
		color: var(--akcentLight)
		text-align: right
	&__btn
		display: flex
		justify-content: flex-end
		button
			margin-left: 2rem
			width: 80px
			height: 40px
			font-size: 1.5rem
			&:disabled
				opacity: .5
#sfbeat
	color: var(--akcentLight)
	font-size: 1.2rem
	text-transform: uppercase
