@import "~@/assets/styles/sass/style.sass"





























































































.policy
	display: flex
	flex-direction: column
	justify-content: space-between
	h2
		line-height: 3.5rem
	&__content
		height: 70%
		overflow-y: auto

		h3
			font-size: 2rem
			margin-bottom: 1rem
			font-weight: 800
		p
			font-size: 1.4rem
			margin: 1rem 0rem
		ul
			font-size: 2rem
			li
				font-size: 1.4rem
		p, li
			line-height: 2rem
		h3,p,li
			width: 95%
	a
		align-self: flex-end
		text-transform: uppercase
