@import "~@/assets/styles/sass/style.sass"


















































































.top-bar
	width: 100%
	height: 8%
	display: flex
	justify-content: space-between
	.beats-set
		font-size: 1.5rem
		font-weight: 800
		&__content
			display: flex
			align-items: center
			background-color: var(--mainBg)
			position: fixed
			width: 100%
			height: 100%
			top: 0%
			left: 0%
			z-index: 4
			padding: 0rem 4rem
			+MW500
				padding: 0rem
.menu
	z-index: 3
.menu-content
	position: absolute
	top: 0%
	left: 0%
	width: 100%
	height: 100%
	background-color: var(--mainBg)
	z-index: 2
	display: flex
	flex-direction: column
	justify-content: center
	align-items: center
	font-size: 2.5rem
	text-transform: uppercase
	+MW414
		top: -10%
		left: -10%
		width: 120%
		height: 120%
	a
		margin: 1.5rem 0rem
		transition: .4s
		&:hover
			color: var(--akcentLight)
			text-shadow: 0px 0px 25px var(--akcentLight)
			transition: .4s
			cursor: pointer
	.userName
		margin-bottom: 4rem
		color: var(--akcentLight)
	.logOut
		display: flex
		align-items: center
		color: var(--akcentLight)
		margin-top: 4rem
		span
			font-size: 1.4rem
			font-weight: 800
		img
			width: 2rem
			height: 2rem
			margin-left: 1rem
	#authorize
		color: var(--akcentLight)
		margin-top: 4rem
		font-size: 1.8rem
		font-weight: 800
