@import "~@/assets/styles/sass/style.sass"




































.songs
	&__items
		overflow: auto
		height: 100%
		width: 100%
		display: flex
		flex-direction: column
		align-items: flex-start
		.song
			width: 95%
			display: grid
			grid-template-columns: 3fr 1fr 1fr 1fr
			padding: 2rem
			justify-content: space-between
			align-items: center
			margin: 1.5rem 0rem
			border-radius: 10px
			&__title
				font-size: 1.5rem
				font-family: "Roboto"
				width: 95%
				text-overflow: ellipsis
				overflow: hidden
				white-space: nowrap
			&__bpm
				font-size: 1.5rem
				font-weight: 800
			&__beat
				font-size: 1.5rem
			&__load
				font-size: 1.3rem
				font-weight: 800
				text-transform: uppercase
				color: var(--akcentLight)
				text-align: center
	&__add-song
		margin: 3rem 0rem
.back-arrow
	font-size: 4rem
	color: var(--akcentLight)
