@import "~@/assets/styles/sass/style.sass"


































































#app
	width: 100%
	height: 100%
	background-color: var(--mainBg)
	font-family: Arial, Helvetica, sans-serif
.infoMessage
	top: 50%
	left: 50%
	transform: translate(-50%,-50%)
	position: fixed
	width: 25rem
	background-color: rgba(0,0,0,.8)
	border-radius: 10px
	z-index: 1000
	border-radius: 5px
	border: 2px solid var(--akcentLight)
	display: flex
	justify-content: center
	align-items: center
	span
		color: var(--akcentLight)
		font-size: 2rem
		padding: 2rem
		font-weight: 800
		text-transform: uppercase
		width: 100% 
		text-align: center
