@import "~@/assets/styles/sass/style.sass"
































































.authType__content
	width: 100%
	height: 100%
	display: flex
	flex-direction: column
	justify-content: center
	align-items: center
.page__title
	text-align: right
#loginGoogle, #loginFacebook, #email, #noAuthorize
	width: 100%
	display: grid
	grid-template-columns: 1fr 1fr
	justify-content: space-between
	align-items: center
	margin-bottom: 2rem
	padding: 0rem 8rem
	img
		width: 3rem
		height: 3rem
		justify-self: center
	span
		justify-self: start
		font-size: 1.3rem
		text-transform: uppercase
		font-weight: 800
		text-align: center
		color: var(--akcentLight)
#noAuthorize
	width: 100%
	color: var(--greenCustom)
	button
		width: 100%
		text-transform: uppercase
		font-size: 1.2rem
