@import "~@/assets/styles/sass/style.sass"




























































































































.metronome-body
	width: 100%
	height: 75%
	display: grid
	grid-template-columns: 1fr
	justify-items: center
	align-items: center
	color: var(--mainGrey)
	position: relative
.volume
	width: 100%
	&__container
		display: flex
		justify-content: space-between
		align-items: center
	img
		width: 3rem
		height: 3rem
	label
		width: 100%
		display: flex
		justify-content: flex-end
	input
		width: 98%
		height: 7px
		-webkit-appearance: none
		background-color: var(--mainGrey)
		border-radius: 0%
	input[type=range]::-webkit-slider-thumb
		-webkit-appearance: none
		height: 15px
		width: 35px
		background-color: var(--akcentLight)
		border: solid 1px var(--akcentLight)
		box-shadow: 0px 0px 40px var(--akcentLight)
		&:hover
			cursor: pointer
.beats-side
	width: 100%
	height: 100%
	display: flex
	justify-content: space-between
	flex-direction: column
	&__content
		display: flex
		justify-content: space-evenly
		width: 100%
		height: 100%
	.beat
		background-color: var(--mainGrey)
		width: 100%
		height: 100%
		margin: 0rem .2rem
		border-radius: 2px
		position: relative
		display: flex
		align-items: baseline
		justify-content: center
		&__sfirstMark
			font-size: 1rem
			color: var(--akcentLight)
			height: .3rem
			border-radius: 10px
.title
	font-size: 1.5rem
	color: var(--akcentLight)
	text-transform: uppercase
	font-weight: 600
	text-align: center
	width: 60%
	h2
		text-align: right
		overflow: hidden
		white-space: nowrap
		text-overflow: ellipsis
	img
		width: 2.5rem
		height: 2.5rem
		position: absolute
		bottom: -10%
		left: 110%
.info
	width: 100%
	display: flex
	align-items: center
	justify-content: space-between
	align-self: flex-end
	height: 3rem
.info-offline
	width: 100%
	display: flex
	justify-content: center
	align-items: flex-end
	h2
		font-size: 1.5rem
		color: var(--akcentLight)
		text-transform: uppercase
		font-weight: 800
		letter-spacing: .2rem
.saveUpdate
	display: flex
	align-items: center
	justify-content: center
	font-size: 1rem
	text-transform: uppercase
	margin: 1rem 0rem
	width: 30%
	&__btn
		width: 100%
		border: solid 2px var(--mainGrey)
		padding: .5rem 0rem
		color: var(--mainGrey)
		width: 100%
		display: flex
		justify-content: center

.bpm
	display: flex
	justify-content: center
	align-items: baseline
	span
		font-size: 2.5rem
		font-weight: 800
	.arrow
		font-weight: 800
		font-size: 2rem
		padding: 1rem
		width: 40px
		height: 40px
		border-radius: 5px
		display: flex
		justify-content: center
		align-items: center
		&:first-child
			margin-right: 1.5rem
		&:last-child
			margin-left: 1.5rem
.knob
	width: var(--knobOuter)
	height: var(--knobOuter)
	border-radius: 50%
	position: relative
	display: flex
	justify-content: center
	align-items: center
	box-shadow: 0 5px 15px 2px var(--mainBg), 0 0 0 12px var(--mainGrey)
	z-index: 1
	transform: rotate(45deg)
	align-self: center
//INPUTMODAL//
.bpmModal
	width: 100vw
	height: 100vh
	position: absolute
	background-color: rgba(0,0 ,0 ,0.8)
	z-index: 3
	top: 50%
	left: 50%
	transform: translate(-50%,-50%)
	display: flex
	justify-content: center
	align-items: center
	&__content
		display: flex
		justify-content: center
		align-items: center
		flex-direction: column
		.bpmModalInfo
			color: var(--akcentLight)
			margin-bottom: 3rem
			font-size: 1.5rem
			position: absolute
			top: 20%
		input
			border: solid 1px var(--akcentLight)
			padding: 2rem
			font-size: 3rem
			width: 15rem
			text-align: center
			border-radius: 10px
			color: white
			background-color: transparent
			-webkit-appearance: none
			&:focus
				box-shadow: 0px 0px 35px var(--akcentLight)
		button
			margin-top: 6rem
			&:disabled
				opacity: .3

