@import "~@/assets/styles/sass/style.sass"

































































































#accept
	display: flex
	align-items: center
	justify-content: space-between
	input
		display: none
	label
		font-size: 1rem
	a
		color: var(--akcentLight)
		margin-left: .2rem
	span
		margin-left: 1rem
	#fake
		display: inline-block
		width: 2rem
		height: 2rem
		border: 2px solid var(--akcentLight)
		position: relative
	#fake::before
		content: ''
		position: absolute
		background-color: var(--akcentLight)
		width: 50%
		height: 50%
		top: 50%
		left: 50%
		transform: translate(-50%, -50%)
		opacity: 0
	input:checked + #fake::before
		opacity: 1

#acceptRule
	width: 2rem
	height: 2rem
